<template>
  <div id="legal-record" :class="skins=='days'?'whites':'whites balck'">
    <div class="title bgf8">{{$t('header.fiat')}}{{$t('asset.record')}}</div>
    <div class="filter-box ft14 bgf8">
      <div>
        <span>{{$t('fat.tradeCurrency')}}：</span>
        <span v-for="item in legalList" :key="item.id" :class="{'select':filterPms.id == item.id}" @click="filterPms.id = item.id;getList()">{{item.name}}</span>
        <!-- <span :class="{'select':filterPms.type == 'sell'}" @click="filterPms.type = 'sell';getList()">{{$t('td.buy')}}</span>
        <span :class="{'select':filterPms.type  == 'buy'}" @click="filterPms.type  = 'buy';getList()">{{$t('td.sell')}}</span> -->
      </div>
      <div>
        <span>{{$t('fat.tdType')}}：</span>
        <span :class="{'select':filterPms.type == 'sell'}" @click="filterPms.type = 'sell';getList()">{{$t('td.buy')}}</span>
        <span :class="{'select':filterPms.type  == 'buy'}" @click="filterPms.type  = 'buy';getList()">{{$t('td.sell')}}</span>
      </div>
      <div>
        <span>{{$t('fat.odStatus')}}：</span>
        <span :class="{'select':filterPms.isSure == 0}" @click="filterPms.isSure = 0;getList()">{{$t('td.nofinish')}}</span>
        <span :class="{'select':filterPms.isSure == 1}" @click="filterPms.isSure = 1;getList()">{{$t('td.finished')}}</span>
        <span :class="{'select':filterPms.isSure == 2}" @click="filterPms.isSure = 2;getList()">{{$t('td.ceiled')}}</span>
      </div>
    </div>
    
    <ul class="ft14 bgf8">
      <li v-for="(item,index) in list" :key="index" class="">
        <div class="flex li-t">
          <div class="ft14 bold cblue">
            <span v-if="item.type == 'sell'">{{$t('td.buy')}}</span>
            <span v-else>{{$t('td.sell')}}</span>
            <span>{{item.currency_name}}</span>
          </div>
          <div class="status">
            <router-link :to="{path:'/legalPay',query:{id:item.id}}" v-if="item.is_sure == 0&&item.type=='sell'">{{$t('td.nofinish')}} ></router-link>
            <router-link :to="{path:'/legalPayDetail',query:{id:item.id}}" v-else-if="item.is_sure == 0 &&item.type=='buy'">{{$t('td.nofinish')}} ></router-link>
            <router-link :to="{path:'/legalPayDetail',query:{id:item.id}}" v-else-if="item.is_sure == 1">{{$t('td.finished')}} ></router-link>
            <router-link class="ceilColor" :to="{path:'/legalPayDetail',query:{id:item.id}}" v-else-if="item.is_sure == 2">{{$t('td.ceiled')}} ></router-link>
            <router-link :to="{path:'/legalPayDetail',query:{id:item.id}}" v-else>{{$t('td.payed')}} ></router-link>
           
          </div>
        </div>
        <div class="flex li-b">
          <div>
            <span>{{$t('td.time')}}</span>
            <span>{{item.create_time}}</span>
          </div>
          <div>
            <span>{{$t('td.num')}}（{{item.currency_name}})</span>
            <span>{{item.number || '0.000' | numFilters(3)}}</span>
          </div>
          <div>
            <span>{{$t('td.tradeTotal')}}（CNY）</span>
            <span>{{item.deal_money || '0.000' | numFilters(3)}}</span>
          </div>
        </div>
      </li>
      
    </ul>
    <el-pagination
        layout="prev, pager, next"
        v-if="total > 10"
        :total="total"
        @current-change="handleCurrentChange"
      ></el-pagination>
    <!-- <div class="more" @click="getList(true)" v-if="list.length">加载更多</div>
   
    <div v-else class="nomore">{{$t('td.nomore')}}</div> -->
     
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      filterPms: { type: "none", id: '', page: 1, isSure: "none" },
      total:0,
      skins: localStorage.getItem("skin") || "days",
      legalList:[],
    
    };
  },
  beforeCreate () {
    document.querySelector('html').setAttribute('style', 'background-color:#f7f7f7;')
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
    if (token) {
      this.token = token;
      this.filterPms.id = this.$route.query.id || "";
      this. init();
      this.getList();
    }
  },
  methods: {
    init(){
      var that = this;
      this.$http({
        url: "/api/currency/list",
        data: {},
        type:'get',
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
         that.legalList = res.data.message.legal;
        }
      });
    },
     // 分页
    handleCurrentChange(val) {
      var that = this;
      that.filterPms.page = val;
      that.getList();
    },
    getList() {
      var that = this;
      var pms = {};
      this.list = [];
      pms.currency_id = this.filterPms.id;
      pms.page = this.filterPms.page;
      if (this.filterPms.type != "none") {
        pms.type = this.filterPms.type;
      }
      if (this.filterPms.isSure != "none") {
        pms["is_sure"] = this.filterPms.isSure;
      }
      var i = layer.load();
      this.$http({
        url: "/api/legal_user_deal",
        params: pms,
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          var msg = res.data.message;
          that.total = msg.total;
            if (msg.data.length) {
              this.list = msg.data;

            } else {
              layer.msg(this.$t('td.nomore'));
            }
          
         
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
.whites {
   width: 1200px;
  margin: 66px auto 50px;
  background-color: #fff;
  padding-bottom:30px;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  min-height: 630px;
  position: relative;
  top: 18px;
  > .title {
    margin-bottom: 30px;
    padding: 0 30px;
    line-height: 50px;
    font-size: 16px;
  }
  > .filter-box {
    line-height: 30px;
    span {
      margin-left: 16px;
      cursor: pointer;
      padding-bottom: 3px;
    }
    .select {
      border-bottom: 2px solid #f0b90b;
    }
  }
  > ul {
    padding: 10px 30px;
    margin-top: 20px;
    li {
      padding: 10px;
      margin-top: 10px;
      border-bottom: 1px solid #e6ecf2;
      > div {
        justify-content: space-between;
        line-height: 30px;
      }
    }
  }

  > .more {
    text-align: center;
    padding: 20px;
    cursor: pointer;
  }
  >.nomore{
    padding: 16px;
    text-align: center;
  }
}
.log_wrap{
  overflow: auto;
}
.el-pagination{
  text-align: center;
  margin-top: 30px;
}
.el-pagination .btn-prev,.el-pagination .btn-next{
  display: none;
}
 .el-pager .active{
  color: #f0b90b;
}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
.balck > ul li{
  border-bottom: 1px solid #000;
}
</style>



